#copy-right {
    p {
        font-size: 0.8rem;
    }
}

#footer {
    position: relative;
    background: $footer-bg;
    margin-top: 200px;
    .f-title {
        &:after {
            content: '';
            border-bottom: 3px solid $primary;
            display: block;
            margin: 15px 0 0 60%;
        }
    }
    .line-top {
        background: url(../images/top-bg-footer.svg) no-repeat;
        background-size: 100%;
        height: 125px;
        width: 100%;
        position: absolute;
        top: -80px;
        left: 0;
        z-index: 1;
        a {
            img {
                position: absolute;
                color: $primary;
                left: 50%;
                top: -7px;
                cursor: pointer;
                transform: translate(-50%, -50%);
                @include media-breakpoint-up(xl) {
                    top: 20px;
                }
            }
        }
    }
    .contact-footer {
        background: $primary;
        .border-start {
            border-left: 0 !important;
            border-bottom: var(--bs-border-width) var(--bs-border-style)
                var(--bs-border-color) !important;
            @include media-breakpoint-up(lg) {
                border-left: var(--bs-border-width) var(--bs-border-style)
                    var(--bs-border-color) !important;
                border-bottom: 0 !important;
            }
        }
    }
    .subscibe-form {
        border-radius: 18px;
        select {
            -webkit-appearance: none;
            -moz-appearance: none;
            appearance: none;
            background-image: url(../images/arrow-bottom.png) !important;
            background-origin: content-box;
            background-position: 5px 10px;
            background-repeat: no-repeat;
            background-size: 10px;
        }

        .subscibe-form-bottom {
            button {
                border-radius: 12px;
                bottom: 0.4rem;
                left: 0.4rem;
            }
        }
    }

    .footer-logo {
        height: 50px;
        width: 50px;

        img {
            height: 26px;
            width: 37px;
        }
    }

    .app-context {
        h4 {
            font-size: 1rem;
        }

        p {
            font-size: 0.75rem;
        }
    }

    .direct-app-link {
        background: $body-bg;
        border-radius: 12px;
        font-size: 0.9rem;

        .btn {
            font-size: 0.8rem;
        }
    }
}
