.wrapper-map {
    position: relative;
    border: 15px solid $white;
    .top-cover-map {
        position: absolute;
        width: 100%;
        height: 5px;
        z-index: 1000;
        background: linear-gradient(
            180deg,
            #f9fafc 10.47%,
            rgba(249, 250, 252, 0) 153.93%
        );
        top: 0px;
    }
    .object-map {
        height: 500px;
    }
    .cover-img-info {
        bottom: -20px;
        width: 100%;
        height: 80px;
        z-index: 1000;
        background: linear-gradient(
            180deg,
            rgba(249, 250, 252, 0) 1.93%,
            #fffffff5 50%
        );
    }
}
