.profile-card-inline {
    .wrapper-profile {
        position: relative;
        display: block;
        text-align: center;
        width: 60px;
        margin-left: 5px;
        img {
            width: 60px;
            border-radius: 6px;
            border: 1px solid #fff;
            box-shadow: 0 16.3157px 28.5525px rgba(0, 0, 0, 0.1);
            z-index: 1000;
            position: absolute;
            left: 8%;
        }
        .bg-under {
            background: rgba(0, 198, 225, 0.22);
            width: 39px;
            height: 42px;
            display: block;
            margin: 5px auto;
            position: absolute;
            bottom: 5px;
            top: 30%;
            border-radius: 10px;
            z-index: 0;
            right: 8%;
        }
    }
}
