.input-rate {
    padding: 0 10px;
    font-family: icomoon !important;
    position: relative;
}
.input-rate:not(:checked) > input {
    position: absolute;
    top: 0;
    display: none;
}
.input-rate:not(:checked) > label {
    float: right;
    width: 1em;
    overflow: hidden;
    white-space: nowrap;
    cursor: pointer;
    font-size: 18px;
    margin-right: 4px;
    color: #ccc;
}
.input-rate:not(:checked) > label:before {
    content: '\e94b';
    display: inline-block;
}
.input-rate > input:checked ~ label {
    color: $warning;
}
.input-rate:not(:checked) > label:hover,
.input-rate:not(:checked) > label:hover ~ label {
    color: $warning;
}
.input-rate > input:checked + label:hover,
.input-rate > input:checked + label:hover ~ label,
.input-rate > input:checked ~ label:hover,
.input-rate > input:checked ~ label:hover ~ label,
.input-rate > label:hover ~ input:checked ~ label {
    color: $warning;
}
