#comment-widget {
    .comment-title {
        .bg-right {
            background: url('../images/chat.png') no-repeat;
            width: 70px;
            height: 70px;
            background-size: contain;
            position: absolute;
            top: -5%;
            right: -25px;
            z-index: -1;
            opacity: 0.5;
        }
    }
}

.comment-wrapper {
    background: url('../images/bg-comment-widget.png') no-repeat;
    background-size: contain;
    background-position: center;
    div > .col-sm-12 {
        margin: 0 auto;
    }
}
