    position: absolute;
    left: 0;
    width: 38px;
    height: 36px;
    background: url(../images/icons/tooltip.svg) no-repeat;
    background-position: center;
    bottom: 0;
}
.uplader-rules {
    position: relative;
    &::before {
        display: block;
        content: url(../images/icons/union.svg);
        margin-left: 0.5rem;
        height: 18px;
    }
}

.slider-galeria-thumbs {
    float: left;
    width: 25%;
    @include media-breakpoint-up(lg) {
        width: 20%;
    }
    @include media-breakpoint-down(md) {
        width: 100%;
        float: unset;

    }
}

.slider-galeria {
    float: left;
    width: 75%;
    img{
        width: 100%;
    }
    @include media-breakpoint-up(lg) {
        width: 80%;
    }
    @include media-breakpoint-down(md) {
        float: unset;
        width: 100%;
    }
}

.slider-galeria div,
.slider-galeria-thumbs div {
    outline: 0;
}

.slider-galeria span {
    display: block;
    height: 250px;
    background: #000;
    color: #3498db;
    font-size: 36px;
    line-height: 250px;
    margin: 0 10px 0 10px;
    padding: 2%;
    text-align: center;
}
.slick-slide {
    display: flex !important;
    align-items: center;
    justify-content: center;
}
.slider-galeria-thumbs span {
    display: block;
    margin-bottom: 5px;
    height: 55px;
    background: #3498db;
    color: #000;
    font-size: 14px;
    line-height: 55px;
    text-align: center;
}
.slick-vertical .slick-slide{
    padding: 1rem;
}
.slider-galeria-thumbs .slick-current span {
    background: #000;
    color: #3498db;
}
.slick-slide img {
    max-width: 100%;
}
.ltr {
    direction: ltr !important;
}
