.owl-carousel .owl-item img {
    display: block;
    width: auto;
}
.owl-carousel.owl-rtl .owl-item {
    float: right;
    padding: 15px 0 !important;
}
.about {
    .owl-carousel {
        .owl-nav {
            text-align: center;
            margin-top: 25px;
            button {
                background: $primary !important;
                padding: 5px !important;
                border: 0;
                color: $white !important;
                border-radius: 8px;
                width: 30px;
                margin: 0px 10px;
                height: 30px;
            }
        }
    }
}
