select {
    &.select-custom {
        position: relative;
        -webkit-appearance: none;
        -moz-appearance: none;
        appearance: none;
        background-image: url(../images/arrow-bottom.png) !important;
        background-origin: content-box;
        background-position: 6px 50%;
        background-repeat: no-repeat;
        background-size: 10px;
        height: 50px;
        color: $category-color;
        background-color: $white;
        border: none;
    }
    + .category-dropdown {
        width: 100%;
        border: 0;
        padding: 8px 0 0;
        margin-top: 10px !important;
        box-shadow: 0px 4px 16px rgba(138, 149, 158, 0.35);
        &:before {
            position: absolute;
            display: block;
            content: '';
            border-color: transparent;
            border-style: solid;
            border-bottom-color: #f4f4f5;
            border-width: 11px;
            transform: translate(-50%, 0);
            right: 10%;
            left: auto;
            top: -22px;
        }
        &:after {
            position: absolute;
            display: block;
            content: '';
            border-color: transparent;
            border-style: solid;
            border-width: 10px;
            border-bottom-color: $white;
            transform: translate(-50%, 0);
            right: 10%;
            left: auto;
            top: -20px;
        }
        > div {
            max-height: 250px;
            overflow: auto;
            &:after {
                position: absolute;
                display: block;
                content: '';
                width: 90%;
                height: 50px;
                z-index: 10000;
                background: linear-gradient(
                    180deg,
                    rgba(255, 255, 255, 0.3) 80%,
                    #ffffff 98.89%
                );
                bottom: 0px;
                border-radius: 0 20px 20px;
            }
            ul {
                li {
                    a {
                        color: $category-color;
                        padding: 8px 15px;
                        &:hover {
                            background: $search-hover-color !important;
                        }
                    }
                }
            }
        }
    }
    + .dropdown-menu {
        width: 100%;
        border: 0;
        padding: 8px 0 0;
        margin-top: 10px !important;
        box-shadow: 0px 4px 16px rgba(138, 149, 158, 0.35);
        &:before {
            position: absolute;
            display: block;
            content: '';
            border-color: transparent;
            border-style: solid;
            border-bottom-color: #f4f4f5;
            border-width: 11px;
            transform: translate(-50%, 0);
            right: 10%;
            left: auto;
            top: -22px;
        }
        &:after {
            position: absolute;
            display: block;
            content: '';
            border-color: transparent;
            border-style: solid;
            border-width: 10px;
            border-bottom-color: $white;
            transform: translate(-50%, 0);
            right: 10%;
            left: auto;
            top: -20px;
        }
        > div {
            max-height: 250px;
            overflow: auto;
            &:after {
                position: absolute;
                display: block;
                content: '';
                width: 90%;
                height: 50px;
                z-index: 10000;
                background: linear-gradient(
                    180deg,
                    rgba(255, 255, 255, 0.3) 80%,
                    #ffffff 98.89%
                );
                bottom: 0px;
                border-radius: 0 20px 20px;
            }
            ul {
                li {
                    a {
                        color: $secondary;
                        padding: 8px 15px;
                        &:hover {
                            color: $primary;
                            background: #00c6e114 !important;
                        }
                    }
                }
            }
        }
    }
}
