.designer-card {
    .avatar {
        position: relative;
        display: block;
        text-align: center;
        height: 120px;
        width: 110px;
        margin: 0 auto;
        img {
            width: 100px;
            border-radius: 28px;
            border: 5px solid #fff;
            box-shadow: 0 16.3157px 28.5525px rgba(0, 0, 0, 0.1);
            z-index: 5;
            position: absolute;
            left: 10%;
        }
        .bg-under {
            background: rgba(0, 198, 225, 0.22);
            width: 50px;
            height: 50px;
            z-index: 0;
            display: block;
            margin: 5px auto;
            position: absolute;
            bottom: 5px;
            left: 30%;
            border-radius: 10px;
        }
    }
    .wrapper-icon {
        position: relative;
        &::before {
            content: '';
            height: 26px;
            width: 26px;
            position: absolute;
            bottom: 5px;
            right: 0;
            background: #e5f9fc;
            border-radius: 50%;
            z-index: 0;
            -webkit-border-radius: 50%;
            -moz-border-radius: 50%;
            -ms-border-radius: 50%;
            -o-border-radius: 50%;
        }
        i{
            position: relative;
            z-index: 1;
        }
    }
}
