.wizard-steps {
    .step {
        color: rgba($light, 0.8);

        .step-btn {
            margin: -7px 0;
            padding: 0 0 10px 0;
        }

        .step-circle-line {
            border-color: rgba($light, 0.8) !important;
            border-width: 2px !important;
            width: 40px;
            height: 40px;

            .step-circle {
                width: 30px;
                height: 30px;
                text-align: center;
                line-height: 2.3;
                color: $secondary;
                background-color: rgba($white, 0.8);
            }
        }

        .step-line {
            background-color: rgba($light, 0.8);
            width: calc(100% - 40px);
            height: 2px;
            right: 40px;
            top: 12px;
        }

        .step-title {
            white-space: nowrap;
            font-size: 0.8em;
            right: -15px;
            left: -14px;
        }

        &.step4 {
            .step-title {
                right: 7px;
            }
        }

        &.step3 {
            .step-title {
                right: 0px;
            }
        }

        &.active {
            color: $blue;
            position: relative;

            .step-title {
                color: rgba($primary, 1);
            }

            .step-circle-line {
                border-color: rgba($primary, 1) !important;

                .step-circle {
                    background-color: rgba($white, 1);
                }
            }

            .step-line {
                background-color: rgba($primary, 1);
            }

            &.current {
                &::before {
                    content: '';
                }
            }
        }
    }

    .step-content {
        display: none;

        &.active {
            display: block;
        }
    }
}
