#dimensions-range {
    width: 100%;
    background: #c5c6d5;
    border-radius: 4px;
    border: none;
    box-shadow: none;
    height: 3px;

    .noUi-connect {
        background-color: $primary;
    }

    .noUi-origin {
        .noUi-handle {
            height: 20px;
            width: 20px;
            border: 2px solid $primary;
            border-radius: 100%;
            top: -9px;
            box-shadow: none;
        }

        .noUi-handle:focus {
            outline: none;
        }

        .noUi-handle:after {
            display: none;
        }

        .noUi-handle:before {
            display: none;
        }
    }

    .noUi-origin:last-child {
        right: -3%;
        .noUi-handle {
            background-color: #dff9fb;
        }
    }
}
