.blog-card {
    transition: 0.2s all ease-in-out;
    -webkit-transition: 0.2s all ease-in-out;
    -moz-transition: 0.2s all ease-in-out;
    -ms-transition: 0.2s all ease-in-out;
    -o-transition: 0.2s all ease-in-out;
    box-shadow: 0px 2px 15px rgba(0, 0, 0, 0.1);
    &:hover {
        box-shadow: none;
    }
    figure {
        img {
            border-radius: 14px;
        }
        .figure-hover {
            visibility: visible;
            padding: 15px;
            border-radius: 14px;
            top: 0;
            right: 0;
            left: 0;
            background: rgb(22 93 103 / 33%);
            bottom: 0;
            margin: 5px;
            h2 {
                color: $white;
                overflow: hidden;
                text-overflow: ellipsis;
                width: 91%;
                font-size: 1.2em;
                margin: 15px 0px;
            }
            .figure-info-product {
                position: absolute;
                bottom: 5px;
            }
        }
    }
    .avatar {
        img {
            width: 35px;
        }
    }

    h2,
    h3 {
        font-size: 1.1rem;
    }

    .btn-like {
        line-height: 0;
        background: #f6f6f6;
        border-radius: 4px;
        padding: 5px 6px;
    }
}
