#header {
    &.transparent {
        background: transparent !important;
    }
    .header-search-form {
        background: $input-bg;
        select {
            position: relative;
            -webkit-appearance: none;
            -moz-appearance: none;
            appearance: none;
            background-image: url(../images/arrow-bottom.png) !important;
            background-origin: content-box;
            background-position: 6px 50%;
            background-repeat: no-repeat;
            background-size: 10px;
            height: 100%;
            color: $category-color;
            background-color: $input-bg;
            border: none;
            border-left: 1px solid $border-color;
            border-radius: 0;
        }
        input {
            background-color: $input-bg;
            border: none;
            height: 40px;
            margin: 0 10px;
            &:focus {
                box-shadow: none !important;
            }
        }
        .category-dropdown {
            width: 120%;
            border: 0;
            padding: 8px 0 0;
            margin-top: 10px !important;
            box-shadow: 0px 4px 16px rgba(138, 149, 158, 0.35);
            &:before {
                position: absolute;
                display: block;
                content: '';
                border-color: transparent;
                border-style: solid;
                border-bottom-color: #f4f4f5;
                border-width: 11px;
                transform: translate(-50%, 0);
                right: 10%;
                left: auto;
                top: -22px;
            }
            &:after {
                position: absolute;
                display: block;
                content: '';
                border-color: transparent;
                border-style: solid;
                border-width: 10px;
                border-bottom-color: $white;
                transform: translate(-50%, 0);
                right: 10%;
                left: auto;
                top: -20px;
            }
            > div {
                max-height: 250px;
                overflow: auto;
                &:after {
                    position: absolute;
                    display: block;
                    content: '';
                    width: 90%;
                    height: 50px;
                    z-index: 10000;
                    background: linear-gradient(
                        180deg,
                        rgba(255, 255, 255, 0.3) 80%,
                        #ffffff 98.89%
                    );
                    bottom: 0px;
                    border-radius: 0 20px 20px;
                }
                ul {
                    li {
                        a {
                            color: $category-color;
                            padding: 8px 15px;
                            &:hover {
                                background: $search-hover-color !important;
                            }
                        }
                    }
                }
            }
        }
    }
    .header-bottom-btn {
        display: flex;
        align-items: center;
        justify-content: flex-end;
        .btn {
            box-shadow: rgba(0,0,0,.1) 0 1px 3px 0, rgba(0,0,0,.06) 0 1px 2px 0;
            &.btn-dropdown-login-user {
                background: $white;
                &:focus {
                    span.username {
                        color: $body-color !important;
                    }
                    i.icon-arrow-down-14 {
                        transform: rotate(180deg);
                    }
                }
            }
        }
        .username {
            @include media-breakpoint-down(xl) {
                width: 55px;
            }
            text-overflow: ellipsis;
            overflow: hidden;
            display: inline-flex;
            white-space: nowrap;
        }
        .btn-basket {
            position: relative;
            width: 52px;
            i {
                position: absolute;
                left: 50%;
                top: 50%;
                transform: translate(-50%, -50%);
            }
        }
    }
    &.header-mobile {
        .header-bottom-btn {
            .btn {
                line-height: 1 !important;
            }
        }
        i.menu {
            background-position: cover;
            width: 20px;
            height: 20px;
            background: url(../images/icons/hamburger-active.svg) no-repeat;
        }
    }
}
