.blog-about-image {
    svg {
        margin: -5px -30px;
    }
    .wrapper-content {
        margin: -150px 0;
        .bg-top-right {
            position: absolute;
            background: $warning;
            border-radius: 100%;
            padding: 10px;
            width: 50px;
            height: 50px;
            margin: 20px -21px;
            svg {
                margin: -14px -26px;
                width: 40px;
                display: block;
            }
        }
        .bg-bottom-left {
            position: absolute;
            background: $white;
            border-radius: 100%;
            padding: 10px;
            width: 50px;
            height: 50px;
            margin: 20px -21px;
            bottom: 40px;
            left: 10px;
        }
    }
}
