.links-list {
    position: relative;
    background: #00c6e114;

    > div {
        text-align: center;
        &::before {
            content: '';
            position: absolute;
            width: 122px;
            height: 8px;
            top: 0;
            right: 50%;
            left: auto;
            transform: translate(50%, 0);
            background: $primary;
            border-radius: 0px 0px 8px 8px;
        }
        ul {
            li {
                position: relative;
                a {
                    text-align: right;
                    display: block;
                    position: relative;
                    &::before {
                        content: '';
                        position: absolute;
                        width: 10px;
                        height: 10px;
                        background: #33d1e7;
                        right: -20px;
                        top: 5px;
                        border-radius: 100%;
                    }
                }
            }
        }
    }
}
