.profile-upgrade-form{
    .nav-item.active{
        background-color: $primary;
        span{
            color: $white !important;
        }
    }
}
.profile-upgrade-form,
.mega-menu {
    position: -webkit-sticky;
    top: 0;
    z-index: 1000;

    div > .inline-menu {
        ul.navbar-nav {
            display: flex;
            flex-direction: row;
            align-items: center;
            border-top: 1px solid #f6f6f6;
            padding-top: 10px !important;
            li {
                background: $white;
                margin-left: 10px;
                position: relative;
                color: $nav-color;
                box-shadow: rgba(0, 0, 0, 0.1) 0px 1px 3px 0px, rgba(0, 0, 0, 0.06) 0px 1px 2px 0px;
                i {
                    color: $primary;
                }
                &.active {
                    box-shadow: none;
                    background: $primary;
                    color: $white;
                    i {
                        color: $white;
                    }
                }
                &:last-child {
                    margin-left: 0;
                }
                &.map {
                    i {
                        right: -10px;
                    }
                }

                a {
                    font-size: 0.9rem;
                    transition: 0.2s all ease-in-out;
                    -webkit-transition: 0.2s all ease-in-out;
                    -moz-transition: 0.2s all ease-in-out;
                    -ms-transition: 0.2s all ease-in-out;
                    -o-transition: 0.2s all ease-in-out;
                    i {
                        margin: -2px 0;
                    }
                    &:hover{
                        cursor: pointer;
                    }
                }

                ul {
                    li {
                        box-shadow: none;

                        a {
                            color: $secondary !important;
                            span {
                                line-height: 2;
                                padding-left: 6px;
                                padding-right: 6px;
                                color: $primary !important;
                                i {
                                    color: $primary !important;
                                }
                            }
                        }
                    }
                }
            }
        }
    }
    .border-animate {
        --border-color: var(--main-color-one);
        --border-width: 2px;
        --bottom-distance: 0px;
        color: #666;
        display: inline-block;
        background-image: linear-gradient(
            var(--border-color),
            var(--border-color)
        );
        background-size: 0% var(--border-width);
        background-repeat: no-repeat;
        transition: background-size 0.3s;
        margin: 5px 0;
    }

    .left-menu {
        display: flex;
        flex-direction: row;
        align-items: baseline;

        li {
            a {
                white-space: nowrap;
                font-size: 0.9rem;
            }
        }
    }

    .dropdown-menu {
        min-width: 180px;
        right: -25% !important;
        li {
            border-bottom: 1px solid $body-bg;
            &:last-child {
                border-bottom: 0;
            }
        }
        &.inline-menu {
            right: -7% !important;
            border-radius: 8px;
            box-shadow: 0 4px 16px rgba(138, 149, 158, 0.15);
            top: 18% !important;
            li {
                display: inline-block;
                min-width: 260px;
                a {
                    &:hover,
                    &:focus {
                        background-color: transparent;
                        span {
                            color: $primary !important;
                        }
                    }
                }
            }
        }
    }

    ul.flex-column {
        padding-left: 10px;
        padding-right: 10px;
        li {
            a {
                display: block;
                overflow: hidden;
                padding: 6px 8px 0 0;
                color: $primary;
                &:hover {
                    border-radius: 8px;
                    box-shadow: none;
                    background: #e5f9fc !important;
                    i {
                        color: $primary;
                    }
                }
            }
        }
    }
}

