.cooperate-container-video {
    background: url('../images/bg-cooperate.svg') no-repeat;
    background-size: 100%;
    padding: 5px 200px 100px;
}

h1.cooperate-header {
    .bg-right {
        background: url('../images/plan-top-buy.png') no-repeat;
        background-size: contain;
        right: 30%;
        position: absolute;
        width: 100px;
        height: 100px;
        z-index: -1;
        top: -60%;
        @include media-breakpoint-down(sm) {
            right: 5%;
        }
    }
}
.cooperate-bg-send-file {
    background: url(../images/bg-send-file.svg) no-repeat;
    background-size: contain;
    width: 300px;
    height: 200px;
    position: absolute;
    bottom: 28%;
    right: -40%;
    z-index: 0;
}
