.bg-header-icon {
    background: #fffae6;
    width: 80px;
    height: 80px;
    border-radius: 100%;
    display: inline-flex;
    position: relative;
    i {
        width: 50px;
        height: 50px;
        position: absolute;
        left: 0;
        top: 18px;
    }
    .special {
        left: -45%;
    }
}
