.radio-widget {
    .wrapper-img {
        img {
            width: 100px;
        }
        .radio-over-img {
            position: absolute;
            background: linear-gradient(
                180deg,
                rgba(255, 255, 255, 0) 0%,
                rgba(0, 198, 225, 0.51) 100%
            );
            z-index: 100;
            right: 0;
            top: -10px;
            height: 100%;
            width: 100%;
        }
    }
    .audio-file {
        .audio-play {
            text-align: center;
            width: 60px;
            height: 60px;
        }
    }
}
