#resolution-range {
    width: 304px;
    background: #c5c6d5;
    border-radius: 4px;
    border: none;
    box-shadow: none;
    height: 3px;

    .noUi-connect {
        background-color: $primary;
    }

    .noUi-origin {
        .noUi-handle {
            height: 26px;
            width: 55px;
            border: 2px solid $primary;
            border-radius: 0px 15px 15px 0px;
            top: -13px;
            box-shadow: none;

            .noUi-touch-area {
                text-align: center;
                font-size: 13px;
                color: $primary;
                direction: ltr;
                line-height: 26px;
            }
        }

        .noUi-handle:focus {
            outline: none;
        }

        .noUi-handle-upper {
            border-radius: 15px 0 0 15px;
        }

        .noUi-handle:after {
            display: none;
        }

        .noUi-handle:before {
            display: none;
        }

        left: 105%;
    }

    .noUi-origin:last-child {
        right: 2%;

        .noUi-handle {
            background-color: #dff9fb;
        }
    }
}
