#search-block {
    background: url('../images/bg-search-box.png') $primary;
    background-size: cover;
    padding: 25px 8px;
    margin: 10px 0; 
    color: $white;
    min-height: 500px;
    position: relative;
    @include media-breakpoint-down(sm) {
        margin: 0 -15px !important;
    }
    @include media-breakpoint-up(xl) {
        border-radius: 45px;
        padding: 40px;
    }
    .btn-state-city {
        @include media-breakpoint-up(xs) {
            width: 120px;
        }
        @include media-breakpoint-up(xl) {
            width: 200px;
        }
    }
    h1{
        @include media-breakpoint-down(md) {
            font-size:44px !important;
        }
    }
    .arrow-absolute {
        top: -8%;
        left: 7%;
        width: 170px;
    }
    .header-search-form {
        background: $body-bg;
        padding: 10px 0;
        margin: 0 5px 0;
        @include media-breakpoint-up(xl) {
            margin: 0 100px 2px;
        }
        select {
            position: relative;
            -webkit-appearance: none;
            -moz-appearance: none;
            appearance: none;
            background-image: url(../images/arrow-bottom.png) !important;
            background-origin: content-box;
            background-position: 6px 50%;
            background-repeat: no-repeat;
            background-size: 10px;
            height: 100%;
            color: $category-color;
            background-color: $input-bg-search;
            border: none;
        }
        input {
            background-color: $input-bg-search;
            border: none;
            height: 50px;
            margin: 0 10px;
        }
    }
    .search-header-result {
        width: 100%;
        border: 0;
        padding: 8px 0 0;
        margin-top: 10% !important;
        box-shadow: 0px 4px 16px rgba(138, 149, 158, 0.35);
        &:before {
            position: absolute;
            display: block;
            content: '';
            border-color: transparent;
            border-style: solid;
            border-bottom-color: #f4f4f5;
            border-width: 11px;
            transform: translate(-50%, 0);
            left: 53%;
            right: auto;
            top: -22px;
        }
        &:after {
            position: absolute;
            display: block;
            content: '';
            border-color: transparent;
            border-style: solid;
            border-width: 10px;
            border-bottom-color: $white;
            transform: translate(-50%, 0);
            left: 53%;
            right: auto;
            top: -20px;
        }
        > div {
            max-height: 250px;
            overflow: auto;
            &:after {
                position: absolute;
                display: block;
                content: '';
                width: 90%;
                height: 50px;
                z-index: 10000;
                background: linear-gradient(
                    180deg,
                    rgba(255, 255, 255, 0.3) 80%,
                    #ffffff 98.89%
                );
                bottom: -2px;
                border-radius: 0 20px 20px;
            }
            ul {
                li {
                    a {
                        padding: 12px 20px;
                        &:hover {
                            background: $search-hover-color !important;
                        }
                    }
                }
            }
        }
    }
    .category-dropdown {
        width: 120%;
        border: 0;
        padding: 8px 0 0;
        margin-top: 10px !important;
        box-shadow: 0px 4px 16px rgba(138, 149, 158, 0.35);
        &:before {
            position: absolute;
            display: block;
            content: '';
            border-color: transparent;
            border-style: solid;
            border-bottom-color: #f4f4f5;
            border-width: 11px;
            transform: translate(-50%, 0);
            right: 10%;
            left: auto;
            top: -22px;
        }
        &:after {
            position: absolute;
            display: block;
            content: '';
            border-color: transparent;
            border-style: solid;
            border-width: 10px;
            border-bottom-color: $white;
            transform: translate(-50%, 0);
            right: 10%;
            left: auto;
            top: -20px;
        }
        > div {
            max-height: 250px;
            overflow: auto;
            &:after {
                position: absolute;
                display: block;
                content: '';
                width: 90%;
                height: 50px;
                z-index: 10000;
                background: linear-gradient(
                    180deg,
                    rgba(255, 255, 255, 0.3) 80%,
                    #ffffff 98.89%
                );
                bottom: 0px;
                border-radius: 0 20px 20px;
            }
            ul {
                li {
                    a {
                        color: $category-color;
                        padding: 8px 15px;
                        &:hover {
                            background: $search-hover-color !important;
                        }
                    }
                }
            }
        }
    }
    .line-bottom {
        background: url(../images/background-header.svg) no-repeat;
        background-position: 0;
        background-size: 100%;
        width: 100%;
        position: absolute;
        height: 130px;
        left: 0; 
        @include media-breakpoint-up(md) {
            height: 150px;
        } 
        a {
            img {
                position: absolute;
                color: $primary;
                left: 50%;
                bottom: -10px;
                cursor: pointer;
                transform: translate(-50%, -50%);
            }
        }
    }
    .btn-download {
        box-sizing: border-box;
        background: $warning;
        font-size: 1.1em;
        @include media-breakpoint-up(xl) {
            font-size: 1.5em;
        }
        color: $white;
        padding: 15px 20px;
        border: 5px solid rgba(255, 255, 255, 0.32);
        border-radius: 16px;
    }
}
