.ruls {
    .nav-link {
        display: block !important;
        i {
            transform: rotate(180deg);
        }
        &.collapsed {
            i {
                transform: rotate(0deg);
            }
        }
        &.active {
            background: $primary;
            border-radius: 50px;
            color: $white;
        }
    }
    .ruls-header {
        position: relative;
        .bg-right {
            background: url('../images/ruls-bg-header-right.png') no-repeat;
            background-size: contain;
            right: 25%;
            position: absolute;
            width: 90px;
            height: 90px;
            z-index: -1;
            top: -30%;
        }
    }
    .sidebar {
        background: #e6fafd;
        position: relative;
        @include border-radius(20px);
        overflow: hidden;
    }
    .box-content {
        box-shadow: 0px 4px 44px 0px #00000014;
        &.desktop {
            min-height: 640px;
        }
        @include border-radius(20px);
        p {
            text-align: justify;
            line-height: 1.8;
        }
    }
    .ruls-scroll {
        max-height: 560px;
        overflow: auto;
        &::-webkit-scrollbar-thumb {
            background: $primary;
            border-radius: 10px;
        }
        &::-webkit-scrollbar-track {
            background: $light;
        }
    }
}
