.navbar-mobile {
    .offcanvas-header {
        background: $primary;
        .wrapper-close {
            position: absolute;
            background: url(../images/bg-header-offcanvas.svg);
            background-size: contain;
            left: 5%;
            bottom: -6px;
            border-radius: 5px 5px 0 0;
            width: 126px;
            height: 54px;
            background-repeat: no-repeat;
            text-align: center;
            line-height: 5.8;
            button {
                margin: 0;
            }
        }
    }
    ul.navbar-nav {
        > li {
            background: $white;
            position: relative;
            color: #a2a2a2;
            margin-bottom: 10px;
            box-shadow: 0px 1px 8px 0px rgba(0, 0, 0, 0.08);
            > a {
                background: $primary;
                color: $white;
                i {
                    color: $white;
                    &.icon-arrow-down-14 {
                        transform: rotate(180deg);
                    }
                }
                &.collapsed {
                    background: $white;
                    color: $secondary;
                    i {
                        color: $primary;
                        &.icon-arrow-down-14 {
                            transform: rotate(0);
                        }
                    }
                }
            }
            ul {
                li {
                    margin: 10px 0;
                    a {
                        color: #a2a2a2;
                        span {
                            color: $primary;
                        }
                    }
                }
            }
        }
    }
}
