#contact-widget {
    background: $primary;
    border-radius: 32px;
    position: relative;
    margin-top: 140px;
    margin-bottom: 100px;
    .close-widget {
        position: absolute;
        top: -70px;
        right: 45px;
        @include media-breakpoint-down(sm) {
            top: -80px;
            right: 0px;
        }
    }
    .wrapper-widget {
        overflow: hidden;
        position: relative;
        .title-rounded {
            position: absolute;
            top: 0;
            right: 0;
            background: rgba(255, 255, 255, 0.35);
            width: 60px;
            height: 60px;
        }
        .bg-transparent-widget {
            position: absolute;
            width: 536.02px;
            height: 857.51px;
            left: -100px;
            top: -200px;
            background: rgba(255, 255, 255, 0.13);
            transform: rotate(334deg);
            border-radius: 100%;
        }
    }
    .mark-star {
        position: absolute;
        top: -40px;
        left: 0;
        background: url('../images/mark-laptop.png') no-repeat;
        width: 100px;
        height: 250px;
        background-size: contain;
        @include media-breakpoint-up(xl) {
            top: -70px;
            left: 50px;
            width: 300px;
            height: 250px;
        }
    }
}
