.about {
    .nav-link {
        &.active {
            background: $primary;
            border-radius: 50px;
            color: $white;
        }
    }
    .about-header {
        position: relative;
        .bg-left-star {
            background: url('../images/about-left-bg.svg') no-repeat;
            background-size: contain;
            left: 0;
            position: absolute;
            width: 60px;
            height: 60px;
            z-index: -1;
            opacity: 0.5;
            top: 10%;
        }
        .bg-right-star {
            background: url('../images/about-right-bg.svg') no-repeat;
            background-size: contain;
            right: 0;
            position: absolute;
            width: 60px;
            height: 60px;
            z-index: -1;
            opacity: 0.5;
            top: 10%;
        }
        .bg-right {
            background: url('../images/fav-bg-title-right.png') no-repeat;
            background-size: contain;
            right: 35%;
            position: absolute;
            width: 60px;
            height: 60px;
            z-index: -1;
            opacity: 0.5;
            top: -10%;
        }
        .bg-bottom {
            background: url('../images/about-bg-title-bottom.png') no-repeat;
            background-size: contain;
            right: 35%;
            position: absolute;
            width: 130px;
            height: 130px;
            z-index: -1;
            opacity: 0.5;
            top: 30%;
        }
    }
    .btn-social {
        box-shadow: 0px 4px 8px 0px #0000000f;
        @include border-radius(8px);
        line-height: 0;
        padding: 8px;
        margin: 0 6px 6px;
    }
}
