.header-designer-profile {
    .img-fluid {
        width: 100%;
    }
    .avatar-profile {
        position: relative;
        margin: -30px 0;
        &.top-60 {
            margin: -60px 0;
        }
        .bg-under {
            background: rgba(0, 198, 225, 0.22);
            width: 50px;
            height: 50px;
            display: block;
            margin: 5px auto;
            position: absolute;
            bottom: -14px;
            left: 30%;
            border-radius: 10px;
            z-index: -1;
        }
    }
}
