.filter-widget {
    box-shadow: 0px 4px 20px 0px #0000001a;
    .filter-tags {
        .icon-svg {
            margin-top: 2px;
        }
        .filter-tag {
            &:hover {
                background: rgba(
                    var(--bs-danger-rgb),
                    var(--bs-bg-opacity)
                ) !important;
            }
        }
    }
}
.filter-sort {
    &.active {
        box-shadow: 0 0 0 0.25rem rgba(51, 209, 231, 0.25);
    }
    input {
        &:focus {
            box-shadow: none;
        }
    }
}
