.profile-card {
    display: inline-block;
    min-width: 120px;

    .wrapper-profile {
        position: relative;
        display: block;
        text-align: center;
        height: 120px;
        width: 120px;
        margin: 0 auto;
        .bg-under {
            background: rgba(0, 198, 225, 0.22);
            width: 50px;
            height: 50px;
            z-index: 0;
            display: block;
            margin: 5px auto;
            position: absolute;
            bottom: 5px;
            left: 30%;
            border-radius: 10px;
        }
        img {
            width: 100px !important;
            border-radius: 28px;
            border: 4px solid #fff;
            box-shadow: 0 16.3157px 28.5525px rgba(0, 0, 0, 0.1);
            z-index: 1000;
            position: absolute;
            left: 10%;
        }
    }
    .title {
        white-space: pre-line;
        width: 120px;
        margin: 0 auto 0;
        text-align: center;
    }
}
