.faq {
    .faq-header {
        position: relative;
        .bg-right {
            background: url('../images/faq-bg-header-right.png') no-repeat;
            background-size: contain;
            right: 25%;
            position: absolute;
            width: 60px;
            height: 60px;
            z-index: -1;
            top: -14%;
        }
    }
    .bg-circle {
        position: absolute;
        top: 0;
        background: rgb(52 209 232 / 16%);
        width: 300px;
        height: 300px;
        border-radius: 100%;
        left: -4%;
        z-index: -1;
    }
    .bg-right {
        background: url('../images/bg-dots-faq.svg') no-repeat;
        background-size: contain;
        right: -30px;
        position: absolute;
        width: 100px;
        height: 100px;
        z-index: -1;
        top: -7%;
    }
    .accordion-item {
        box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.1);
        color: var(--#{$prefix}accordion-color);
        background-color: var(--#{$prefix}accordion-bg);
        border: 0;
        margin-bottom: $faq-margin-bottom;

        &:first-of-type {
            @include border-top-radius(12px);
            @include border-bottom-radius(12px);

            .accordion-button {
                @include border-top-radius(12px);
                @include border-bottom-radius(12px);
            }
        }

        &:not(:first-of-type) {
            border-top: 0;
            @include border-top-radius(12px);
            @include border-bottom-radius(12px);
        }

        // Only set a border-radius on the last item if the accordion is collapsed
        &:last-of-type {
            @include border-bottom-radius(12px);
            @include border-top-radius(12px);

            .accordion-button {
                &.collapsed {
                    @include border-bottom-radius(12px);
                    @include border-top-radius(12px);
                }
            }

            .accordion-collapse {
                @include border-bottom-radius(12px);
                @include border-top-radius(12px);
            }
        }
        .accordion-body {
            padding-right: 60px;
            padding-top: 0;
        }
    }
    .accordion-button {
        background: #f1f1f1;
        text-align: right !important;
        @include border-radius(12px);

        &::after {
            display: none;
        }

        &:not(.collapsed) {
            background: 0 0;
            box-shadow: none;
        }

        &.collapsed {
            &::before {
                background-image: url('data:image/svg+xml;base64,PHN2ZyB3aWR0aD0iMTkiIGhlaWdodD0iNSIgdmlld0JveD0iMCAwIDE5IDUiIGZpbGw9Im5vbmUiIHhtbG5zPSJodHRwOi8vd3d3LnczLm9yZy8yMDAwL3N2ZyI+CjxwYXRoIGQ9Ik0wLjcyNDQ2OSAwLjgyNjIwOEgxOC45ODczVjQuMzI4NjZIMC43MjQ0NjlWMC44MjYyMDhaIiBmaWxsPSIjMDBDNkUxIi8+Cjwvc3ZnPgo=');
                margin-top: 8px;
            }
        }

        &::before {
            flex-shrink: 0;
            display: inline-flex;
            float: right;
            width: var(--#{$prefix}accordion-btn-icon-width);
            height: var(--#{$prefix}accordion-btn-icon-width);
            margin-left: 1rem;
            content: '';
            background-image: url('data:image/svg+xml;base64,');
            background-repeat: no-repeat;
            background-size: var(--#{$prefix}accordion-btn-icon-width);
            @include transition(var(--#{$prefix}accordion-btn-icon-transition));
        }
    }
}
