.wrapper-items {
    position: relative;
    padding: 0 40px;

    .owl-dots {
        &:last-child {
            margin-left: 0;
        }

        .owl-dot {
            background: $light;
            width: 10px;
            height: 10px;
            border-radius: 100%;
            margin-left: 10px;

            &.active {
                background: $white;
            }
        }
    }
    .owl-nav {
        button {
            background: $primary !important;
            color: $white !important;
            border-radius: 30%;
            width: 35px;
            height: 35px;
            line-height: 3.5 !important;
            font-size: 0.8em !important;

            &.owl-next {
                position: absolute;
                bottom: 40px;
                top: 40%;
                left: -3px;
                @include media-breakpoint-up(xl) {
                    left: -3px;
                }
            }

            &.owl-prev {
                position: absolute;
                bottom: 40px;
                top: 40%;
                right: -3px;
                @include media-breakpoint-up(xl) {
                    right: -3px;
                }
            }
        }
    }
    &.down {
        .owl-nav {
            text-align: center;
            button {
                top: initial;
                left: initial;
                right: initial;
                bottom: initial;
                &.owl-next {
                    position: relative;
                    margin-right: 5px;
                }

                &.owl-prev {
                    position: relative;
                    margin-left: 5px;
                }
            }
        }
    }
}
