.blog-writer {
    .wrapper-profile {
        position: relative;
        text-align: center;
        width: 120px;
        height: 120px;
        .bg-under {
            background: rgba(0, 198, 225, 0.22);
            width: 50px;
            height: 50px;
            z-index: 0;
            display: block;
            position: absolute;
            bottom: 14px;
            border-radius: 10px;
            margin-right: 18px;
        }
        img {
            width: 95px;
            border-radius: 28px;
            border: 1px solid #fff;
            box-shadow: 0 16.3157px 28.5525px rgba(0, 0, 0, 0.1);
            z-index: 1000;
            position: absolute;
            right: 50%;
            left: auto;
            transform: translate(66%, 5%);
        }
    }
}
