.cart {
    .offer-code {
        .input-offer {
            background: $input-bg;
            padding: 5px;
            @include border-radius(8px);
        }
    }
    .items-cart {
        .item {
            border-bottom: 1px solid $border-color;
            &:last-child {
                border-bottom: 0;
            }
        }
    }
}
