#blog-widget {
    .blog-title {
        .bg-right {
            background: url('../images/blog-bg-right.png') no-repeat;
            width: 70px;
            height: 70px;
            background-size: contain;
            position: absolute;
            top: -5%;
            right: -25px;
            z-index: -1;
            opacity: 0.5;
        }
        .bg-left {
            background: url('../images/blog-bg-left.png') no-repeat;
            width: 50px;
            height: 50px;
            background-size: contain;
            position: absolute;
            top: 10%;
            left: -50px;
        }
    }
}
