#slider {
    background: url('../images/slider-background.png');
    background-repeat: no-repeat;
    background-position: right 0;
    min-height: 400px;

    h1 {
        width: 280px;
        height: 84px;
        font-weight: 800;
        text-align: right;
        letter-spacing: -1px;
    }

    .wrapper-slider {
        background: linear-gradient(90deg, #eef0f5 52.52%, #f9fafc 93.01%);
        border-radius: 18px;

        .owl-dots {
            position: absolute;
            left: 50%;
            transform: translate(-50%, -50%);
            bottom: 5%;

            &:last-child {
                margin-left: 0;
            }

            .owl-dot {
                background: $light;
                width: 10px;
                height: 10px;
                border-radius: 100%;
                margin-left: 10px;

                &.active {
                    background: $white;
                }
            }
        }

        .owl-nav {
            position: absolute;
            bottom: 40px;
            left: 30px;

            button {
                background: $body-bg;
                box-shadow: $box-shadow-sm;
                border-radius: 40%;
                width: 30px;
                height: 30px;
                line-height: 3;
                font-size: 0.9em;

                &.owl-prev {
                    margin-left: 6px;
                }
            }
        }
    }
}
