.blog-video-item {
    .video-wrapper {
        position: relative;
        background: $white;
        @include border-radius(20px);
        text-align: center;
        margin: 5px;
        &.minimal {
            img {
                @include border-radius(10px);
            }
            .btn-play {
                padding: 10px;
                text-decoration: none;
                .btn-action {
                    padding: 10px !important;
                    font-size: 1em !important;
                }
            }
        }
        img {
            width: 100%;
            @include border-radius(20px);
        }
        .btn-play {
            border: none;
            background: #ffffff87;
            @include border-radius(100%);
            padding: 15px;
            .btn-action {
                z-index: 1000;
                background: $white;
                padding: 30px;
                font-size: 3em;
                line-height: 0;
                @include border-radius(100%);
            }
            &:hover {
                background: #ffffff3c;
            }
        }
    }
}
