.plans {
    .plan-item {
        &.plan-item-info {
            .btn-check:checked + .btn,
            .btn.active,
            .btn.show,
            .btn:first-child:active,
            :not(.btn-check) + .btn:active {
                color: $white !important;
                background-color: $info !important;
                border-color: $info !important;
                * {
                    color: $white !important;
                }
            }
            .btn-check + .btn:hover {
                border-color: $info;
            }
        }
        &.plan-item-warning {
            .btn-check:checked + .btn,
            .btn.active,
            .btn.show,
            .btn:first-child:active,
            :not(.btn-check) + .btn:active {
                color: var(--bs-body-color) !important;
                background-color: $warning !important;
                border-color: $warning !important;
                * {
                    color: var(--bs-body-color) !important;
                }
            }
            .btn-check + .btn:hover {
                border-color: $warning;
            }
        }
        &.plan-item-primary {
            .btn-check:checked + .btn,
            .btn.active,
            .btn.show,
            .btn:first-child:active,
            :not(.btn-check) + .btn:active {
                color: $white !important;
                background-color: $primary !important;
                border-color: $primary !important;
                * {
                    color: $white !important;
                }
            }
            .btn-check + .btn:hover {
                border-color: $primary;
            }
        }
        .plan-title {
            position: absolute;
            margin-top: -30px;
            &.bg-primary {
                box-shadow: 0px 4.52343px 15.832px rgba(0, 198, 225, 0.5);
            }
            &.bg-warning {
                box-shadow: 0px 4px 14px rgba(255, 202, 5, 0.5);
            }
            &.bg-info {
                box-shadow: 0px 4px 14px rgba(162, 65, 238, 0.5);
            }
        }
        .plan-order {
            position: absolute;
            white-space: nowrap;
            margin-bottom: -30px;
        }
    }
    ul {
        li {
            list-style: none;
        }
    }
}
