#favorite-widget {
    .fav-title {
        .bg-right {
            background: url('../images/fav-bg-title-right.png') no-repeat;
            width: 50px;
            height: 50px;
            background-size: contain;
            position: absolute;
            top: 8%;
            right: -5px;
            z-index: -1;
            opacity: 0.5;
        }
        .bg-left {
            background: url('../images/fav-bg-title-left.png') no-repeat;
            width: 80px;
            height: 80px;
            background-size: contain;
            position: absolute;
            top: -35%;
            left: -60px;
        }
    }
}
