.blog-article-links {
    position: relative;
    background: rgba(249, 249, 249, 1);

    > div {
        text-align: center;
        h6 {
            position: relative;
            z-index: 1;
            min-height: 40px;
            &::before {
                display: block;
                content: '';
                background: $primary;
                border-radius: 100%;
                width: 40px;
                height: 40px;
                position: absolute;
                top: -10px;
                right: -26px;
                z-index: 0;
            }
            span {
                display: inline-flex;
                z-index: 1;
                position: absolute;
                right: -16px;
            }
        }
        ul {
            li {
                position: relative;
                a {
                    text-align: right;
                    display: block;
                    position: relative;
                    &::before {
                        content: '';
                        position: absolute;
                        width: 10px;
                        height: 10px;
                        background: $primary;
                        right: -20px;
                        top: 5px;
                        border-radius: 100%;
                    }
                }
            }
        }
    }
}
