.custom-owl {
    position: relative;

    .owl-dots {
        padding: 0;
        margin: 25px auto;
        position: relative;
        display: block;
        text-align: center;
        z-index: 2;
        max-width: 100px;
        overflow: hidden;
        height: 20px;
        &:last-child {
            margin-left: 0;
        }
        .owl-dot {
            background: $light;
            width: 10px;
            height: 10px;
            border-radius: 100%;
            margin-left: 10px;
            &:last-child {
                margin-left: 0;
            }
            &.active {
                background: $primary !important;
            }
        }
    }

    .owl-nav {
        margin: 20px auto;
        text-align: center;
        display: block;
        width: 175px;
        position: relative;
        z-index: 2;
        button {
            background: transparent !important;
            color: $white !important;
            border-radius: 30%;
            width: 35px;
            height: 35px;
            line-height: 3.5 !important;
            font-size: 0.8em !important;

            &.owl-next {
                float: right;
            }

            &.owl-prev {
                float: left;
            }
        }
    }
}
