.widget-avatar-profile {
    position: relative;
    display: inline-block;
    filter: drop-shadow(0px 2.88095px 21.6071px rgba(0, 0, 0, 0.06));
    .bg-under {
        background: rgba(0, 198, 225, 0.22);
        width: 50px;
        height: 14px;
        display: block;
        margin: 5px auto;
        bottom: -12px;
        border-radius: 0 0 10px 10px;
        position: absolute;
        right: 50%;
        left: auto;
        transform: translate(50%, 0);
    }
}
