.dropdown-login-user {
    min-width: 250px;

    .wrapper-profile {
        position: relative;
        display: block;
        text-align: center;
        width: 60px;
        height: 70px;
        right: 10px;
        img {
            width: 50px;
            border-radius: 6px;
            border: 1px solid #fff;
            box-shadow: 0 16.3157px 28.5525px rgba(0, 0, 0, 0.1);
            z-index: 1000;
            position: absolute;
            left: 20%;
            top: 15%;
        }
        .bg-under {
            background: rgba(0, 198, 225, 0.22);
            width: 39px;
            height: 42px;
            display: block;
            margin: 5px auto;
            position: absolute;
            bottom: 5px;
            top: 30%;
            border-radius: 10px;
            z-index: 0;
            right: 8%;
        }
    }
}
.dropdown-login-user ,
#sidebar-dashboard {
    li {
        padding:0 .5rem ;
        span {
            padding: 8px 14px;
        }
        &:not(.first){
            position: relative;
            &::before{
                width: 4px;
                height: 100%;
                content: '';
                border-top-left-radius: 10px;
                border-bottom-left-radius: 10px;
                position: absolute;
                background-color: transparent;
                top: 0;
                right: 0;
            }
        }
        &.active,
        &:hover{
            &::before{
                background-color: $primary;
            }
            a {
                color: $primary;
            }
        }
        a {
            font-weight: 700;
            padding: 8px 14px;
            margin: 5px 0;
            color: #787878;
        }
    }
}
#sidebar-dashboard{
    li{
        padding: 0 .7rem;
        a {
            padding: 8px 10px;
            margin: 0;
        }
        &:not(.disabled)::before{
            width: 6px;
        }
    }
}
