.table-support {
    thead {
        background: #f6f6f6;
        border-color: $white;
        tr {
            th {
                padding: 15px;
                &:first-child {
                    border-radius: 0 10px 10px 0;
                }
                &:last-child {
                    border-radius: 10px 0 0 10px;
                }
            }
        }
    }
}
