#profil,
.profile-upgrade-form {
    .wrapper-avatar {
        background: rgba($primary, 0.1);
        border-radius: valid-radius(8px 8px 50px 50px);
        min-height: 100px;
    }
    hr{
        height: 10px !important;
        border: 0;
        background: #F1F1F1;
        opacity: 1;
    }
    .progress{
        background-color: #F1F1F1;
        height: 5px;
    }
    .avatar-profile {
        position: relative;
        display: block;
        text-align: right;
        height: 120px;
        margin: -55px 25px;
        width: 120px;
        img {
            width: 100px;
            border-radius: 28px;
            border: 1px solid #fff;
            box-shadow: 0 16.3157px 28.5525px rgba(0, 0, 0, 0.1);
            z-index: 1000;
            position: absolute;
            left: 10%;
        }
        .bg-under {
            background: rgba(0, 198, 225, 0.22);
            width: 50px;
            height: 50px;
            z-index: 0;
            display: block;
            margin: 5px auto;
            position: absolute;
            bottom: 5px;
            left: 30%;
            border-radius: 10px;
        }
    }
    .leftbar-menu {
        .nav-link {
            i {
                background: transparent;
                padding: 14px 8px;
                margin: 0 0 0 10px;
                border-radius: 13px;
                width: 50px;
                height: 50px;
                text-align: center;
                font-size: 1.3rem;
                color: $secondary;
            }

            &.purple-link {
                i {
                    color: $purple;
                    background: rgba($purple, 0.2);
                }

                &.active {
                    .label-link {
                        color: $purple;
                    }
                }
            }

            &.green-link {
                i {
                    color: $green;
                    background: rgba($green, 0.2);
                    padding: 15px 13px;
                    font-size: 1rem;
                }

                &.active {
                    .label-link {
                        color: $green;
                    }
                }
            }

            &.yellow-link {
                i {
                    color: $yellow;
                    background: rgba($yellow, 0.2);
                    font-size: 1.2rem;
                }

                &.active {
                    .label-link {
                        color: $yellow;
                    }
                }
            }

            &.red-link {
                i {
                    color: $red;
                    background: rgba($red, 0.2);
                }

                &.active {
                    .label-link {
                        color: $red;
                    }
                }
            }

            &.blue-link {
                i {
                    color: $primary;
                    background: rgba($primary, 0.2);
                }

                &.active {
                    .label-link {
                        color: $primary;
                    }
                }
            }
        }
    }
}

.multi-select .form-control {
    background-color: transparent;
}

.multi-select__box {
    border-radius: 5px;
    width: 100%;
}
.multi-select .form-control:focus {
    color: #939393;
    background-color: transparent;
    box-shadow: none !important;
}
.multi-select__inner-box {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    flex-wrap: wrap;
    width: 100%;
}

.multi-select__inner-box input {
    border: 0 !important;

    width: auto;
}

.multi-select__container {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    justify-content: flex-start;
    border-radius: 0.375rem;
    width: 100%;
    min-height: 36.5px;
    position: relative;
    z-index: 2;
}

.multi-select__container input {
    border: 0 !important;
    width: auto;
}

.multi-select__item {
    padding: 6px 10px;
    display: flex;
    align-items: center;
    font-size: 14px !important;
    line-height: 22px !important    ;
    justify-content: flex-start;
    height: 30px !important;
    margin-left: 4px;
}

.multi-select__item i {
    font-size: 11px;
    margin-left: 8px;
}

.multi-select__item:hover {
    cursor: pointer;
}

.multi-select__items {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    flex-wrap: wrap;
}

.multi-select .list-group-item {
    padding: 0.3rem 1rem;
    background: var(--bs-primary) !important;
    border-radius: 4px;
    color: $white;
    border: 0 !important;
    margin-left: 6px;
    :hover {
        cursor: pointer;
    }
}

.multi-select__result {
    box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px,
        rgba(60, 64, 67, 0.15) 0px 1px 3px 1px;
    max-height: 198px;
    position: absolute;
    background-color: $white;
    border-radius: 6px;
    padding: 10px !important;
    margin: 0 !important;
    z-index: 100;
    display: none;
}

.multi-select__loading {
    display: none;
}

.multi-select__loading--show {
    display: flex;
}
a {
    text-decoration: none !important;
}
.multi-select.is-active .multi-select__box {
    position: absolute;
    top: 0;
    z-index: 2;
    left: 0;
    right: 0;
}

.multi-select.is-active .multi-select__result {
    width: 100%;
    display: flex;
}
.multi-select .control-label {
    display: none;
}
.multi-select-50 {
    height: 50px;
}
.ti-close {
    background-image: url('../../images/close.svg');
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center;
    width: 12px;
    height: 12px;
}





.step-container {
    position: relative;
    text-align: center;
    transform: translateY(-43%);
}

.step{
    display: none;
}
.step-circle {
    width: 52px;
    height: 52px;
    border-radius: 50%;
    background-color: #fff;
    border: 6px solid #F1F1F1;
    line-height: 52px;
    font-weight: bold;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 10px;
    padding-top: 5px;
    cursor: pointer;

    &.active{

        background-color: $primary;
        border-color:#80E3F0 !important;
        color: $white;
    }
    &.current{
        border-color:$primary;
    }
    span{
        bottom: -50px;
        width: 120px;
    }
}

.step-line {
    position: absolute;
    top: 16px;
    left: 50px;
    width: calc(100% - 100px);
    height: 2px;
    background-color:$primary;
    z-index: -1;
}

#multi-step-form {
    overflow-x: hidden;
}