.comment-rate {
    input {
        visibility: hidden;
        position: absolute;
        z-index: 0;
    }
    .points {
        margin: 10px 0 20px 0;
        display: flex;
        text-align: center;
        .target {
            position: relative;
            padding: 0 0 0 50px;
            color: $light;
            .circle {
                background: $light;
                width: 20px;
                height: 20px;
                border-radius: 100%;
            }
            &:before {
                content: '';
                height: 3px;
                background: $light;
                width: 100%;
                display: block;
                position: absolute;
                top: 23%;
                right: -50px;
                background: $light;
            }
            &:first-child {
                &:before {
                    display: none;
                }
            }
            &.active {
                color: $warning;
                div {
                    background: $warning;
                }
                &:before {
                    background: $warning;
                }
            }
        }
    }
}
