.blog-context-widget {
    .bg-top-right {
        position: absolute;
        background: rgba(255, 202, 5, 0.12);
        border-radius: 100%;
        padding: 10px;
        width: 50px;
        height: 50px;
        margin: 20px -21px;
    }
    .bg-bottom-left {
        position: absolute;
        background: rgba(0, 198, 225, 0.07);
        border-radius: 100%;
        padding: 10px;
        width: 50px;
        height: 50px;
        margin: 20px -21px;
        bottom: 40px;
        left: 10px;
    }
}
