.sort-widget {
    background: $widget-tags-bg;
    @include border-radius(12px);
    button {
        background: transparent;
        color: $secondary !important;
        &.active {
            background: $white;
            color: $dark !important;
            box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.06);
        }
    }
}
