.card,
.video-card {
    transition: 0.2s all ease-in-out;
    -webkit-transition: 0.2s all ease-in-out;
    -moz-transition: 0.2s all ease-in-out;
    -ms-transition: 0.2s all ease-in-out;
    -o-transition: 0.2s all ease-in-out;
    box-shadow: 0px 2px 15px rgba(0, 0, 0, 0.1);
    &:hover {
        box-shadow: none;
    }
    .video-wrapper {
        position: relative;
    }
    figure {
        img {
            border-radius: 14px;
        }
        &:hover {
            .figure-hover {
                visibility: visible;
            }
        }
        .figure-hover {
            visibility: hidden;
            padding: 15px;
            border-radius: 14px;
            top: 0;
            right: 0;
            left: 0;
            background: #00000059;
            bottom: 0;
            margin: 5px;
            h2 {
                color: $white;
                overflow: hidden;
                text-overflow: ellipsis;
                width: 91%;
                font-size: 1.2em;
                margin: 15px 0px;
            }
            button {
                height: 38px;
            }
            .figure-info-product {
                position: absolute;
                bottom: 20px;
            }
            .user-info-inline {
                margin-top: 35px;
            }
            .user-profile {
                position: relative;
                display: block;
                text-align: center;
                width: 50px;
                margin-left: 5px;
                img {
                    width: 40px;
                    border-radius: 6px;
                    border: 1px solid $white;
                    box-shadow: 0 16.3157px 28.5525px rgba(0, 0, 0, 0.1);
                    z-index: 1000;
                    position: absolute;
                    right: 5px;
                }
            }
        }
    }
    .avatar {
        img {
            width: 35px !important;
        }
    }

    h2,
    h3 {
        font-size: 1.1rem;
    }

    .btn-like {
        line-height: 0;
        background: #f6f6f6;
        border-radius: 4px;
        padding: 5px 6px;
    }
}
