.profile-widget {
    .profile-title {
        .bg-right {
            background: url('../images/profile-title-bg-right.png') no-repeat;
            width: 60px;
            height: 60px;
            background-size: contain;
            position: absolute;
            top: -120%;
            right: 14%;
            z-index: -1;
            opacity: 0.5;
        }
        .bg-left {
            background: url('../images/profile-title-bg-left.png') no-repeat;
            width: 60px;
            height: 60px;
            background-size: contain;
            position: absolute;
            top: -145%;
            left: -60px;
        }
    }
}
