.video-card {
    position: relative;
    background: $white;
    @include border-radius(20px);
    text-align: center;
    padding: 15px;
    &.minimal {
        padding: 10px;
        img {
            @include border-radius(10px);
        }
        .btn-play {
            padding: 10px;
            text-decoration: none;
            .btn-action {
                padding: 15px !important;
                font-size: 2em !important;
            }
        }
    }
    img {
        width: 100%;
        @include border-radius(20px);
    }
    .btn-play {
        border: none;
        background: #ffffff87;
        @include border-radius(100%);
        padding: 15px;
        .btn-action {
            z-index: 1000;
            background: $white;
            padding: 30px;
            font-size: 3em;
            line-height: 0;
            @include border-radius(100%);
        }
        &:hover {
            background: #ffffff3c;
        }
    }
    &.grid {
        padding: inherit;
        .btn-play {
            border: none;
            background: rgba(103, 103, 103, 0.28);
            backdrop-filter: blur(2px);
            @include border-radius(100%);
            padding: 15px;
            .btn-action {
                background: transparent;
                z-index: 1000;
                padding: 20px;
                font-size: 3em;
                line-height: 0;
                @include border-radius(100%);
                i {
                    color: $white !important;
                }
            }
            &:hover {
                backdrop-filter: blur(0);
            }
        }
    }
}
