.select2-container--search-template {
    min-width: 150px;
    .selection {
        display: block;
        background: $input-bg-search;
        @include border-radius($input-border-radius, 0);
        color: $secondary;
        height: 50px;
        text-align: start;
        padding: 15px;
    }

    .select2-selection__arrow {
        display: block;
        position: absolute;
        left: 11px;
        top: 15px;
        font-size: 20px;
        font-family: icomoon !important;
        color: $primary;
        font-style: normal;
        font-weight: 400;
        font-variant: normal;
        text-transform: none;
        line-height: 1;
        -webkit-font-smoothing: antialiased;
        &:before {
            content: '';
        }
    }
    .select2-dropdown {
        @include border-radius($input-border-radius, 0);
        border: 0;
        box-shadow: 0px 9px 44px rgba(0, 0, 0, 0.12);
        .select2-results {
            padding: 8px;
            .select2-results__option {
                padding: 6px 8px;
                @include border-radius($input-border-radius, 0);
                color: $secondary;
                &:hover {
                    color: $primary;
                    background: #00c7e130;
                }
            }
        }
    }
}
.select2-container--header-search-template {
    min-width: 150px;
    .selection {
        display: block;
        background: transparent;
        @include border-radius(0, 0);
        color: $secondary;
        height: 40px;
        text-align: start;
        padding: 10px;
        border-left: 1px solid $light;
    }

    .select2-selection__arrow {
        display: block;
        position: absolute;
        left: 11px;
        top: 10px;
        font-size: 18px;
        font-family: icomoon !important;
        color: $secondary;
        font-style: normal;
        font-weight: 400;
        font-variant: normal;
        text-transform: none;
        line-height: 1;
        -webkit-font-smoothing: antialiased;
        &:before {
            content: '';
        }
    }
    .select2-dropdown {
        @include border-radius($input-border-radius, 0);
        border: 0;
        box-shadow: 0px 9px 44px rgba(0, 0, 0, 0.12);
        .select2-results {
            padding: 8px;
            .select2-results__option {
                padding: 6px 8px;
                @include border-radius($input-border-radius, 0);
                color: $secondary;
                &:hover {
