                }
            }
        }
    }
}
.select2-container--filter-search-template {
    margin: 8px 0 5px !important;
    min-width: 100%;
    .selection {
        display: block;
        background: transparent;
        @include border-radius(4, 4);
        color: $secondary;
        height: 40px;
        text-align: start;
        padding: 10px;
        box-shadow: 1px 2px 10px $shadow-custom-color;
    }

    .select2-selection__arrow {
        display: block;
        position: absolute;
        left: 11px;
        top: 10px;
        font-size: 18px;
        font-family: icomoon !important;
        color: $secondary;
        font-style: normal;
        font-weight: 400;
        font-variant: normal;
        text-transform: none;
        line-height: 1;
        -webkit-font-smoothing: antialiased;
        &:before {
            content: '';
        }
    }
    .select2-dropdown {
        @include border-radius($input-border-radius, 0);
        border: 0;
        margin: auto;
        box-shadow: 0px 9px 44px rgba(0, 0, 0, 0.12);
        .select2-results {
            padding: 8px;
            .select2-results__option {
                padding: 6px 8px;
                @include border-radius($input-border-radius, 0);
                color: $secondary;
                &:hover {
                    color: $primary;
                    background: #00c7e130;
                }
            }
        }
    }
}
.select2-container--grid-dropdown-template {

    min-width: 100%;
    .selection {
        display: block;
        background: transparent;
        @include border-radius(8px, 8px);
        color: $primary;
        height: 40px;
        text-align: start;
        padding: 10px 0 ;
        border: 1px solid $primary;
        &__rendered{
            padding-left: 36px;
        }
    }
    .select2-selection__arrow {
        display: block;
        position: absolute;
        left: 8px;
        top: 10px;
        font-size: 18px;
        padding-right: 3px;
        font-family: icomoon !important;
        color: $primary;
        font-style: normal;
        border-right: 1px solid $primary;
        font-weight: 400;
        font-variant: normal;
        text-transform: none;
        line-height: 1;
        -webkit-font-smoothing: antialiased;
        &:before {
            content: '';
        }
    }
    .select2-selection--single:focus-visible {
        outline: none !important;
    }
    .select2-dropdown {
        @include border-radius($input-border-radius, 0);
        border: 0;
        margin: auto;
        box-shadow: rgba(0,0,0,.1) 0 1px 3px 0, rgba(0,0,0,.06) 0 1px 2px 0;
        .select2-results {
            padding: 8px;
            .select2-results__option {
                font-weight: bold;
                padding: 6px 8px;
                @include border-radius($input-border-radius, 0);
                color: $secondary;
                &:hover {
                    color: $primary;
                    background: #00c7e130;
                }
            }
        }
    }
}

.dashboard-grid-filters{
    input{
        border: 1px solid $primary;
        height: 40px;
        border-radius: 8px;
    }
}

.multi-step-form{

    .default-select-2{
        +.select2-container--default {
            width: 100% !important;
            .selection {
                display: block;
                @include border-radius($input-border-radius, 0);
                color: $secondary;
                height: 36px;
                text-align: start;
                padding: 0;
            }
            .select2-selection__arrow {
                display: block;
                position: absolute;
                left: 8px !important;
                top: 8px;
                font-size: 20px;
                text-transform: none;
                line-height: 1;
                -webkit-font-smoothing: antialiased;
                &:before {
                    content: url('../images/icons/select-box-arrow.svg');
                }
                b{
                    display: none;
                }
            }
            .select2-selection--single{
                border: 0;
                background: #f1f1f1;
                border-radius: 0.5rem;
                line-height: 36px;
                height: 36px;
            }
            .select2-selection__rendered{
                line-height: 36px
            }
            .select2-dropdown {
                border: 0;
                .select2-results {
                    border-bottom-right-radius: 4px;
                    border-bottom-left-radius: 4px;
                    padding: 8px;
                    .select2-results__option {
                        padding: 6px 8px;
                        @include border-radius($input-border-radius, 0);
                        color: $secondary;
                        &:hover {
                            color: $white;
                            background: $primary;
                        }
                    }
                }
            }
            .select2-results__option:last-child{
                border-bottom-right-radius: 4px;
                border-bottom-left-radius: 4px;
            }

        }
    }
}
.select2-container--default {
    &:focus-visible {
        outline: none !important;
    }
    .select2-dropdown {
        border: 0;
        border: 1px solid #eee;
        .select2-results {
            border-bottom-right-radius: 4px;
            border-bottom-left-radius: 4px;
            padding: 0;
            .select2-results__option {
                padding: 6px 8px;
                border-radius: 0;
                color: $secondary;
                background-color: $white;
                &:hover {
                    color: $white;
                    background: $primary;
                }
                &.select2-results__option--highlighted{
                    color: $white;
                    background: $primary;
                }
            }
        }
    }
    .select2-results__option:last-child{
        border-bottom-right-radius: 4px !important;
        border-bottom-left-radius: 4px !important;
    }

}