i {
    &.icon-svg {
        display: inline-flex;
        background-position: cover;
        width: 22px;
        height: 20px;
    }
    &.menu {
        background: url('../images/icons/menu.svg') no-repeat;
        &.active {
            background: url('../images/icons/menu-active.svg') no-repeat;
        }
    }
    &.notification {
        background: url('../images/icons/notification.svg') no-repeat;
        &.active {
            background: url('../images/icons/notification-blue.svg') no-repeat;
        }
    }
    &.support {
        background: url('../images/icons/messege-2.svg') no-repeat;
        &.active {
            background: url('../images/icons/messege-2-blue.svg') no-repeat;
        }
    }
    &.plan {
        background: url('../images/icons/graph.svg') no-repeat;
        &.active {
            background: url('../images/icons/graph-blue.svg') no-repeat;
        }
    }
    &.comment {
        background: url('../images/icons/comment.svg') no-repeat;
        &.active {
            background: url('../images/icons/comment-blue.svg') no-repeat;
        }
    }
    &.download {
        background: url('../images/icons/download-1.svg') no-repeat;
        &.active {
            background: url('../images/icons/download-1-blue.svg') no-repeat;
        }
    }
    &.print {
        background: url('../images/icons/printer.svg') no-repeat;
        &.active {
            background: url('../images/icons/printer-blue.svg') no-repeat;
        }
    }
    &.favorite {
        background: url('../images/icons/heart-menu.svg') no-repeat;
        &.active {
            background: url('../images/icons/favorite-blue.svg') no-repeat;
        }
    }
    &.profile {
        background: url('../images/icons/user.svg') no-repeat;
        &.active {
            background: url('../images/icons/user-blue.svg') no-repeat;
        }
    }
    &.logout {
        background: url('../images/icons/exit.svg') no-repeat;
        &.active {
            background: url('../images/icons/exit-blue.svg') no-repeat;
        }
    }
    &.arrow-left-btn {
        background: url('../images/icons/arrow-left-btn.svg') no-repeat;
    }

    &.screenmirroring {
        background: url('../images/icons/screenmirroring.svg') no-repeat;
        &.active {
            background: url('../images/icons/screenmirroring-blue.svg')
                no-repeat;
        }
    }
    &.ruler-pen {
        background: url('../images/icons/ruler-pen.svg') no-repeat;
        &.active {
            background: url('../images/icons/ruler-pen-blue.svg') no-repeat;
        }
    }
    &.hashtag {
        background: url('../images/icons/hashtag.svg') no-repeat;
        &.active {
            background: url('../images/icons/hashtag-blue.svg') no-repeat;
        }
    }
    &.receive-square {
        background: url('../images/icons/receive-square.svg') no-repeat;
        &.active {
            background: url('../images/icons/receive-square-blue.svg') no-repeat;
        }
    }
    &.calendar {
        background: url('../images/icons/calendar.svg') no-repeat;
        &.active {
            background: url('../images/icons/calendar-blue.svg') no-repeat;
        }
        &.small {
            background-size: 85%;
        }
    }
    &.document-text {
        background: url('../images/icons/document-text.svg') no-repeat;
        &.active {
            background: url('../images/icons/document-text-blue.svg') no-repeat;
        }
    }
    &.group-user {
        background: url('../images/icons/group-user.svg') no-repeat;
    }
    &.star-yellow {
        background: url('../images/icons/star-yellow.svg') no-repeat;
    }
    &.brush {
        background: url('../images/icons/brush.svg') no-repeat;
        &.active {
            background: url('../images/icons/brush-blue.svg') no-repeat;
        }
    }
    &.trash {
        background: url('../images/icons/trash.svg') no-repeat;
        &.active {
            background: url('../images/icons/trash-blue.svg') no-repeat;
        }
    }
    &.filter {
        background: url('../images/icons/filter.svg') no-repeat;
        &.active {
            background: url('../images/icons/filter-blue.svg') no-repeat;
        }
    }
    &.microphone {
        background: url('../images/icons/microphone.svg') no-repeat;
        &.active {
            background: url('../images/icons/microphone-blue.svg') no-repeat;
        }
    }
    &.filter-2 {
        background: url('../images/icons/filter-2.svg') no-repeat;
        &.active {
            background: url('../images/icons/filter-2-blue.svg') no-repeat;
        }
    }
    &.play-border {
        background: url('../images/icons/play-border.svg') no-repeat;
        &.active {
            background: url('../images/icons/play-border-blue.svg') no-repeat;
        }
    }
    &.time {
        background: url('../images/icons/time.svg') no-repeat;
        &.small {
            background-size: 85%;
        }
    }
    &.add-white {
        background: url('../images/icons/add-white.svg') no-repeat;
    }
    &.docs-list {
        &.active {
            background: url('../images/icons/docs-list-active.svg') no-repeat;
        }
    }
    &.close {
        background: url('../images/icons/close.svg') no-repeat;
        &.gray {
            background: url('../images/icons/close-gray.svg') no-repeat;
        }
        &.xsmall {
            width: 13px;
            height: 13px;
        }
    }
    &.star-line {
        &.white {
            background: url('../images/icons/star-line-white.svg') no-repeat;
        }
        &.small {
            width: 18px;
            height: 18px;
        }
    }
}

#sidebar-dashboard,
.dropdown-login-user {
    li{
        &.active,
        &:hover {
            a {
                i {
                    &.menu {
                        background: url('../images/icons/menu-active.svg') no-repeat;
                    }
                    &.notification {
                        background: url('../images/icons/notification-blue.svg')
                            no-repeat;
                    }
                    &.support {
                        background: url('../images/icons/messege-2-blue.svg')
                            no-repeat;
                    }
                    &.plan {
                        background: url('../images/icons/graph-blue.svg') no-repeat;
                    }
                    &.comment {
                        background: url('../images/icons/comment-blue.svg')
                            no-repeat;
                    }
                    &.download {
                        background: url('../images/icons/download-1-blue.svg')
                            no-repeat;
                    }
                    &.print {
                        background: url('../images/icons/printer-blue.svg')
                            no-repeat;
                    }
                    &.favorite {
                        background: url('../images/icons/heart-menu-blue.svg')
                            no-repeat;
                    }
                    &.profile {
                        background: url('../images/icons/user-blue.svg') no-repeat;
                    }
                    &.logout {
                        background: url('../images/icons/exit-blue.svg') no-repeat;
                    }
                }
            }
        }
    }
}
