.product-slider {
    .owl-nav {
        position: absolute;
        top: 50%;
        width: 100%;
        button {
            display: block;
            width: 30px;
            height: 30px;
            margin: 0 30px;
            font-size: 1.5em !important;
            background: $white !important;
            border-radius: 100% !important;
            padding: 10px !important;
            &.owl-prev {
                float: right;
            }
            &.owl-next {
                float: left;
            }
        }
    }
}
